import React from 'react';
import globalStyles from '@common/style';
import courses from '@data/courses';

import '@components/NewCourse/index.scss';

//TYPES
import { CourseType } from '@data/interfaces/courses.interfaces';
const NewCourseData: CourseType = courses[0];
class NewCourse extends React.Component {
    videoWidth = '350px';
    videoHeight = '200px';
    constructor(props) {
        super(props);
        this.videoWidth = window.innerWidth < 500 ? "350px" : "430px";
        this.videoHeight = window.innerWidth < 500 ? "200px" : "248px";
    }
    componentDidMount() {
    }
    render() {
        return (
            <div className="middle-content" style={{
                background: NewCourseData?.primaryColor || "#215733",
                padding: "10px 0px 10px 0px"
            }}>
                <div className="container" style={{ minHeight: "300px" }}>
                    <div className="row" style={{
                        minHeight: "300px"
                    }}>
                        <div className="col-lg-7 col-md-12" style={{ padding: "20px 20px" }}>
                            <div className="d-flex align-items-center justify-content-start"
                                style={{
                                    minHeight: "300px",
                                    maxWidth: "100%"
                                }}>
                                <div style={{
                                    color: "white",
                                    fontSize: "30px",
                                    fontFamily: 'inherit',
                                    fontWeight: "bolder"
                                }}>
                                    <div style={{
                                        color: "black",
                                        backgroundColor: "white",
                                        width: "100px",
                                        fontSize: "15px",
                                        textAlign: "center",
                                        marginBottom: "10px"
                                    }}>Yeni EÄŸitim</div>
                                    <div style={{
                                        overflow: "hidden",
                                        textOverflow: "ellipsis",
                                        height: "40px",
                                        color: NewCourseData?.secondaryColor
                                    }}>
                                        {NewCourseData?.title || "Node.js EÄŸitimi - SÄ±fÄ±rdan Profesyonele NodeJs"}
                                    </div>
                                    <div style={{
                                        marginTop: "15px",
                                        fontSize: "17px",
                                        fontWeight: "lighter"
                                    }}>
                                        {NewCourseData?.description}
                                    </div>
                                    <div>
                                        <p style={{ textAlign: "left" }}>
                                            <a
                                                href={`${NewCourseData?.url}?referralCode=${NewCourseData?.referralCode}&couponCode=${NewCourseData?.couponCode}`}
                                                //href="https://www.udemy.com/course/nodejs-egitimi-sifirdan-profesyonele/?referralCode=0017AFE73990096201B4&couponCode=CANGOKCEASLAN"
                                                target="_blank"
                                                className="btn btn-secondary px-4 py-2 btn-sm"
                                                style={{
                                                    fontWeight: "bold",
                                                    backgroundColor: "white",
                                                    marginLeft: "0px",
                                                    borderWidth: "0px !important",
                                                    textTransform: "none",
                                                    fontSize: "18px",
                                                    borderColor: "white",
                                                    color: "black",
                                                    marginTop: "20px",
                                                    letterSpacing: "0.5px"
                                                }}>Hemen Kursu Al</a>

                                        </p>
                                    </div>
                                </div>
                                <div>

                                </div>
                            </div>

                        </div>
                        <div className="col-lg-5 col-md-12" style={{ padding: "0px 20px" }}>
                            <div className="new-course-video d-flex align-items-center justify-content-center" style={{
                                minHeight: "300px"
                            }}>
                                <div style={{
                                    border: "2px solid white",
                                    padding: "0px !important",
                                    margin: "0px",
                                    maxHeight: `calc(${this.videoHeight} + 4px)`,
                                    backgroundColor: "white",
                                    marginTop: "35px",
                                }}>
                                    <iframe width={this.videoWidth}
                                        height={this.videoHeight}
                                        src={`${NewCourseData?.youtubeEmbedUrl || "https://www.youtube.com/embed/h-Xx14d39vk"}?fs=1&iv_load_policy=3&showinfo=0&rel=0&cc_load_policy=0&start=0&end=0&mute=0&loop=1&color=white&controls=0&modestbranding=1&playsinline=1&rel=0&enablejsapi=1`}
                                        title="YouTube video player"
                                        frameBorder="0"
                                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                        allowFullScreen></iframe>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        )
    }

}

export default NewCourse;